@import "../../App.scss";

.quote {
	padding-top: 100px;
}

.wrapper {
	max-width: 880px;
	margin: 0 auto;
}

.title {
	text-align: center;
}

.subtitle {
	text-align: center;
	max-width: 900px;
	margin: 26px auto;
	padding: 0 10px;
}

.info {
	display: flex;
	justify-content: space-between;
	align-items: center;

	&.left {
		flex-direction: row;
	}

	&.right {
		flex-direction: row-reverse;
	}

	@include tablet {
		flex-direction: column-reverse !important;
	}

	.text {
		text-align: left;
		margin: 0 32px;

		@include tablet {
			margin-top: 20px;
		}

		h3 {
			font-size: 18px;
			font-weight: 500;
			line-height: 21.94px;
		}

		p {
			margin-top: 18px;
			font-size: 16px;
			font-weight: 400;
			line-height: 19.5px;
		}
	}

	.img {
		width: 320px;
		height: 337.33px;
		border-radius: 10px;
	}
}
